import React, { Component } from 'react';
import {canleIon,copyText} from '../../public';
import '../../style.css';

import './invite.css';

class Invite extends Component{
	constructor(props) {
    super(props);
    this.state = {
		urls:'wwww.1234.com',
		code:'115df1e2',
		copyText:''
    };
    
  }
	cancel(e){
		this.props.callBack('cancelInvite');
	}
	copy(){
		alert('复制成功!');
	}

	render(){
		let copyText=Math.random();
		let lang=localStorage.getItem("lang");//语种
  		lang=lang?lang:"CN";
		return (
			<div className="eos-recarge-wrap invite-wrap">
				<input id="copy" type='text' value={this.state.urls+':'+this.state.code}/>
				<div className="eos-box">
					<div className='canle' onClick={this.cancel.bind(this)}>
						<img src={canleIon}/>
					</div>
					<h3 className="title">{lang=="CN"?"邀请好友":"Invite Friends"}</h3>
					<div className="input-box flex">
						<span>{lang=="CN"?"网址":"website"}:</span>
						<span className="label label-primary">www.1234.com</span>
					</div>
					<div className="input-box flex">
						<span>{lang=="CN"?"邀请码":"Code"}:</span>
						<span className="label label-primary">115df1e2</span>
					</div>
					<button id="copy-btn" type="button" data-clipboard-text={copyText+"www.1234.com:115"} onClick={this.copy.bind(this)} className="copy-btn btn btn-primary" data-toggle="tooltip" data-placement="top" title={lang=="CN"?"复制成功":"Replication succeeded"}>{lang=="CN"?"一键复制":"One-click replication"}</button>
					<div className="tip">{lang=="CN"?"邀请成功,立即获取下注额的0.5%返现":"Get 0.5% reward from every HongBao after inviting a friend"}</div>
					
				</div>
			</div>
		)
	}
}
export default Invite;