import React, { Component } from 'react';
import '../../style.css';
import './alerts.css';
import EOS from 'eosjs';
const TABLE_RED_PAPER="redenvelope";//红包表
const TABLE_RED_STATE="gameruntime";//红包状态表
const TABLE_GET_RED_PAPER="grabre";//抢红包表
const TABLE_GAME_CONFIG="gameconf";//游戏配置表
const TABLE_PLAY_BALANCE="playbalance";//游戏玩家表
let contract_name="gre12345game";
let account_name="gre53palyer3";
let account_name2="gre53palyer2";
let account_name3="gre53palyer3";
const CONFIG={
	httpEndpoint:'http://www.super.lighting:8888',
	code_as_wasm: true,
	chain_id:'cf057bbfb72640471fd910bcb67639c22df9f92470936cddc1ade0e2f2e7dc4f',
	
//	keyProvider:["PW5KAHS48gxwJeGjd2xh8RMqDpbPSH2SeLuYBNBdpoM2HyL6pt6W2"],
	keyProvider:['5KQwrPbwdL6PhXujxW37FSSQZ1JiwsST4cqQzDeyXtP79zkvFD3','5Jmsawgsp1tQ3GD6JyGCwy1dcvqKZgX6ugMVMdjirx85iv5VyPR']
}
class Alerts extends Component{
	constructor(props) {
    super(props);
    this.state = {
		val:''
    };
   this.loadAccountTable=this.loadAccountTable.bind(this);
  }
	loadAccountTable(row){//
		let eos=EOS(CONFIG),_this=this;
		eos.getTableRows({"scope":contract_name,"code":contract_name,"lower_bound":row*10,"table":TABLE_PLAY_BALANCE,"json":true}).then(res=>{
			let data=res.rows;
			for(let i=0;i<data.length;i++){
				if(data[i].player==_this.state.val){
					localStorage.setItem('account_name',_this.state.val);
					alert('登录成功');
					window.location.reload();
					return 
				}
			}
			res.more?_this.loadAccountTable(row+1):alert('用户名错误')
		})
	}
	goLogin(){
		if(!this.state.val){
			alert('用户名不能为空');
			return 
		}
		this.loadAccountTable(0);
	}
	cancelLogin(){
		this.props.callBack('cancelLogin');
	}
	changeInput(){
		this.setState({val:this.refs.account_name.value});
	}
	render(){
		let lang=localStorage.getItem("lang");//语种
  		lang=lang?lang:"CN";
		return (
			<div className="alert-wrap">
				<div className="alert-box">
					<div className="alert-cancel">
					<span onClick={this.cancelLogin.bind(this)} className="glyphicon glyphicon-remove"></span>
					</div>
					
					<div className="alert-content" style={{"text-align":"center"}}>
						<form>
						  <div className="form-group">
						    <label for="exampleInputEmail1">{lang=="CN"?"登录":"Login"}</label>
						    <input onChange={this.changeInput.bind(this)} type="text" className="form-control" ref="account_name" id="exampleInputEmail1" placeholder={lang=="CN"?"请输入您的账户":"Please enter your account"}/>
						  </div>
						  <button type="button" className="btn btn-primary btn-lg active" onClick={this.goLogin.bind(this)}>{lang=="CN"?"登录":"Login"}</button>
							
						</form>
					</div>
				</div>
			</div>
		)
	}
}
export default Alerts;