import React, { Component } from 'react';
import {rankTheme1,rankTheme2,rankTheme3} from '../../public';
class RankList extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	theme:props.theme
    };
    
  }
	render(){
		let themeSrc;
		let theme=this.state.theme;
		if(theme==1){
			themeSrc=rankTheme1
		}else if(theme==2){
			themeSrc=rankTheme2
		}else if(theme==3){
			themeSrc=rankTheme3
		}
		return (
			<div className="rank-item-wrap">
				<div className="rank-item flex">
					<div>
						{themeSrc?<img src={themeSrc}/>:theme}	
					</div>
					<div>
						{this.props.data.player}
					</div>
					<div>
						{this.props.data.balance}
					</div>
					<div>
						{this.props.data.re_amount+'SYS'}
					</div>
				</div>
			</div>
		)
	}
}
export default RankList;