import React, { Component } from 'react';
import {glIcon} from '../../public'
class List extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	src:props.src,
    	theme:props.theme?props.theme:'',//1 有箭头，默认没有箭头
    	handClick:props.callBack,
    	keys:props.key,
    	title:props.title
    };
    
 }
componentWillMount(){
//  this.props.callBack('9----09090****90888888**');
 }
	render(){
		let elemArr=[
			<div className="flex-start" key={this.state.key}>
						<span className="right">6.22</span>
						<img className="gl" src={glIcon}/>
					</div>
		];
		let classN=this.state.theme==1?'isgl menu-item flex ':'menu-item flex ';
		return (
			<div className={classN} onClick={this.state.handClick.bind(this)}>
				<div className="flex-start">
						<div className="img-box">
							<img src={this.state.src}/>
						</div>
						<span>{this.state.title}</span>
					</div>
					{this.state.theme==1?elemArr:''}
				</div>
		)
	}
}
export default List;