import React, { Component } from 'react';
import {roomListBg1,roomListBg2,roomListBg3,roomListBg4,userSrc} from '../../public';
import './roomlist.css';
class RoomList extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	data:props.data?props.data:{}
    	,theme:props.theme?props.theme:1
    	
    };
    
  }
  render() {
  	let lang=localStorage.getItem("lang");//语种
  	lang=lang?lang:"CN";
  	let roomListBg,theme=this.state.theme;
  	if(theme==1){
  		roomListBg=roomListBg1
  	}else if(theme==2){
  		roomListBg=roomListBg2
  	}else if(theme==3){
  		roomListBg=roomListBg3
  	}else if(theme==4){
  		roomListBg=roomListBg4
  	}
  	
    return (
      <div className="room-list-wrap" onClick={this.props.callBack.bind(this)}>
      	<img className="room-list-bg" src={roomListBg}/>
      	<ul className="room-content-box">
      		<li className="room-content-item">
      			<span>{lang=="CN"?"5人抢红包":"Manual in 5"}</span>
      			<span className={'label label-default label-'+this.state.theme}>{lang=="CN"?"22EOS场":"22 EOS Room"}</span>
      		</li>
      		<li className="flex">
      			<div>{lang=="CN"?"从天而将的红包,总有一个是你的":"Enjoy the grabbing exitement"}</div>
      			<div>
      				<img className="user-img" src={userSrc}/>
      				<span>43</span>
      			</div>
      		</li>
      	</ul>
      </div>
    );
  }
}
export default RoomList;