import React, { Component } from 'react';
import {canleIon} from '../../public';
import '../../style.css';
import './eosRecarge.css';
import EOS from 'eosjs';
const TABLE_RED_PAPER="redenvelope";//红包表
const TABLE_RED_STATE="gameruntime";//红包状态表
const TABLE_GET_RED_PAPER="grabre";//抢红包表
const TABLE_GAME_CONFIG="gameconf";//游戏配置表
const TABLE_PLAY_BALANCE="playbalance";//游戏玩家表
let contract_name="gre12345game";
let account_name="gre53palyer3";
let account_name2="gre53palyer2";
let account_name3="gre53palyer3";
const CONFIG={
	httpEndpoint:'http://www.super.lighting:8888',
	code_as_wasm: true,
	chain_id:'cf057bbfb72640471fd910bcb67639c22df9f92470936cddc1ade0e2f2e7dc4f',
	
//	keyProvider:["PW5KAHS48gxwJeGjd2xh8RMqDpbPSH2SeLuYBNBdpoM2HyL6pt6W2"],
	keyProvider:['5KQwrPbwdL6PhXujxW37FSSQZ1JiwsST4cqQzDeyXtP79zkvFD3','5Jmsawgsp1tQ3GD6JyGCwy1dcvqKZgX6ugMVMdjirx85iv5VyPR']
}
class eosRecarge extends Component{
	constructor(props) {
    super(props);
    this.state = {
		account_info:{},
		val:''
    };
    this.loadAccount=this.loadAccount.bind(this);
    this.changeInput=this.changeInput.bind(this);
    this.depositBalance=this.depositBalance.bind(this);
  }
	depositBalance(deposit){
		let _this=this;
		let account_name=localStorage.getItem('account_name');
//		console.log(deposit,this.state.account_name);
//		if(this.state.account_name==""){
		if(!account_name){
			alert('请登陆');
			return
		}else if(!deposit){
			alert("请输提现金额");
			return
		}
		
		let eos=EOS(CONFIG);
		eos.contract(contract_name).then(contract=>{
			console.log(contract);
			console.log('99999999')
			console.log(JSON.stringify(contract.withdraw));
			console.log('99999999')
			contract.withdraw(account_name,deposit,{authorization:[account_name]}).then(res=>{
				console.log(res);
			}).catch(err=>{
				console.log('err')
				console.log(err);
			});
		});
	}
	loadAccount(row,account_name){
		let eos=EOS(CONFIG);
		let _this=this;
		eos.getTableRows({"scope":contract_name,"code":contract_name,"lower_bound":row*10,"table":TABLE_PLAY_BALANCE,"json":true}).then(res=>{
			let rows=res.rows;
			for(let i=0;i<rows.length;i++){
				let obj=rows[i];
				if(obj.player==account_name){
					_this.setState({account_info:obj});
					return ''
				}
			}
			if(res.more){
				_this.loadAccount(row+1,account_name);
			}
		})
	}
	changeInput(){
		this.setState({val:this.refs.msg.value});
	}
	componentWillMount(){
		let account_name=localStorage.getItem('account_name');
		
		this.loadAccount(0,account_name);
	
	}
	cancel(e){
		this.props.callBack('cancelEos');
	}
	render(){
		let lang=localStorage.getItem("lang");//语种
  		lang=lang?lang:"CN";
		return (
			<div className="eos-recarge-wrap">
				<div className="eos-box">
					<div className='canle' onClick={this.cancel.bind(this)}>
						<img src={canleIon}/>
					</div>
					<h3>{lang=="CN"?"EOS帐余额":"EOS account balance"}: {this.state.account_info.balance?this.state.account_info.balance:'----'}</h3>
					<div className="input-box flex">
						<input ref="msg" type="text" onChange={this.changeInput} value={this.state.val} placeholder={lang=="CN"?"请输入充值数量":"Please enter the amount of recharge."}/>
						<span>eos</span>
					</div>
					<div className="eos-tip">({lang=="CN"?"需要输入eos数量":"Need to enter eos quantity"})</div>
					<div className="flex-around">
						<div className="btn btn-default">
							{lang=="CN"?"充值":"Deposit"}
						</div>
						<div className="btn btn-default" onClick={this.depositBalance.bind(this.state.val)}>
							{lang=="CN"?"提现EOS":"Withdraw EOS"}
						</div>
					</div>
				</div>
			</div>
		)
	}
}
export default eosRecarge;