import React, { Component } from 'react';
import {superRedbacket2x,superRedBacketText2x,menu2x,stockSrc,signInIocn} from '../public';
import RoomList from '../comment/roomlist/roomlist';
import Rank from '../comment/rank/rank';
import Menu from '../comment/menu/menu';
import Eoselem from '../comment/eosRecarge/eosRecarge';
import Invite from '../comment/invite/invite';
import Loading from '../comment/loading/loading';
import Login from '../comment/alerts/alertLogin';
import EOS from 'eosjs';
const TABLE_RED_PAPER="redenvelope";//红包表
const TABLE_RED_STATE="gameruntime";//红包状态表
const TABLE_GET_RED_PAPER="grabre";//抢红包表
const TABLE_GAME_CONFIG="gameconf";//游戏配置表
const TABLE_PLAY_BALANCE="playbalance";//游戏玩家表
let contract_name="gre12345game";
let account_name="gre53palyer3";
let account_name2="gre53palyer2";
let account_name3="gre53palyer3";
const CONFIG={
	httpEndpoint:'http://www.super.lighting:8888',
	code_as_wasm: true,
	chain_id:'cf057bbfb72640471fd910bcb67639c22df9f92470936cddc1ade0e2f2e7dc4f',
	
//	keyProvider:["PW5KAHS48gxwJeGjd2xh8RMqDpbPSH2SeLuYBNBdpoM2HyL6pt6W2"],
	keyProvider:['5KQwrPbwdL6PhXujxW37FSSQZ1JiwsST4cqQzDeyXtP79zkvFD3','5Jmsawgsp1tQ3GD6JyGCwy1dcvqKZgX6ugMVMdjirx85iv5VyPR']
}
class App extends Component {
	constructor(props) {
    super(props);
    this.state = {
    	addMenu:[],
    	addEos:[],
    	addInvite:[],
    	addLoading:[],
    	addLogin:[],
    	lang:"CN",
    	rankData:[]
    };
    this.gameData={
    	rankData:[]//账号表每页临时数据
    	,rank:[]//账号表所有临时数据
    	,redPaperLimit:[]//抢红包每页临时数据
    	,rank_re:[]//抢红包所有临时数据
    }
    this.loadRank=this.loadRank.bind(this);
    this.loadAccountTable=this.loadAccountTable.bind(this);
    this.loadRedPaperTable=this.loadRedPaperTable.bind(this);
  }
 createComparisonFunctionByProperty(propertyName) { //根据指定属性名的值，按照升序排序
    return function(object1, object2){
        var value1 = parseFloat(object1[propertyName]);
        var value2 = parseFloat(object2[propertyName]);
        if (value1 < value2){
            return 1;
        } else if (value1 > value2){
            return -1;
        } else{
	    return 0;
        }
    };
} 
	loadRank(row){
		let num=0,_this=this;
		_this.menuCallBack('openLoading');
		let stop=setInterval(()=>{
			num++;
			console.log("帐户/抢红包 表、、..",num);
			if(_this.gameData.rank.length>0&&_this.gameData.rank_re.length>0){
				clearInterval(stop);
				_this.menuCallBack('cancelLoading');
				console.log('帐户、抢红包表加载成功...');
				let arr=_this.gameData.rank;
				let arr_re=_this.gameData.rank_re;
				let arr_rank=[];
				for(let i=0;i<arr.length;i++){
					let obj=arr[i];
					obj.re_amount=0;
					for(let j=0;j<arr_re.length;j++){
						let obj_re=arr_re[j];
						if(obj.player==obj_re.re_player){
							obj.re_amount=parseFloat(obj.re_amount)+parseFloat(obj_re.re_amount);
							obj.re_amount=Math.floor(obj.re_amount*10000)/10000;
						}
					}
					arr_rank.push(obj);
				}
				_this.setState({
					rankData:arr_rank.sort(_this.createComparisonFunctionByProperty("re_amount"))
				});
//				arr.sort(_this.createComparisonFunctionByProperty("balance"));
//				console.log(arr_rank.sort(_this.createComparisonFunctionByProperty("re_amount")));
				console.log(_this.state.rankData);
			}else if(num>10){
				clearInterval(stop);
				_this.menuCallBack('cancelLoading');
				console.log(_this.gameData.rank_re);
				console.log(_this.gameData.rank)
				console.log('帐户表加载 失败...');
			}
		},1000);
		_this.loadAccountTable(0);
		_this.loadRedPaperTable(0);
	}
	loadRedPaperTable(row){
		let eos=EOS(CONFIG),_this=this;
		eos.getTableRows({"scope":contract_name,"code":contract_name,"lower_bound":row*10,"table":TABLE_GET_RED_PAPER,"json":true}).then(res=>{
			let data=res.rows;
			if(res.more){
				_this.gameData.redPaperLimit=_this.gameData.redPaperLimit.concat(data);
				_this.loadRedPaperTable(row+1);
			}else{
				_this.gameData.rank_re=_this.gameData.redPaperLimit.concat(data);
//				let arr=_this.gameData.redPaperLimit.concat(data);
//				for(let i=0;i<arr.length;i++){
//					
//				}
				
				_this.gameData.redPaperLimit=[];
			}
		})
	}
	loadAccountTable(row){//加载用户表
		let eos=EOS(CONFIG),_this=this;
		eos.getTableRows({"scope":contract_name,"code":contract_name,"lower_bound":row*10,"table":TABLE_PLAY_BALANCE,"json":true}).then(res=>{
			let data=res.rows;
			if(res.more){
				
				_this.gameData.rankData=_this.gameData.rankData.concat(data);
				_this.loadRank(row+1);
			}else{
				_this.gameData.rank=_this.gameData.rankData.concat(data);

//				_this.setState({
//					rankData:_this.gameData.rankData.sort(_this.createComparisonFunctionByProperty("balance"))
//				});
				_this.gameData.rankData=[];
			}
		})
	}
	menuCallBack(type){
		if('openEos'==type){

			this.setState({addMenu:[],addEos:[<Eoselem callBack={this.menuCallBack.bind(this)}></Eoselem>]});
		}else if('cancelEos'==type){
			this.setState({addEos:[]});

		}else if('openMenu'==type){

			this.setState({addMenu:[<Menu callBack={this.menuCallBack.bind(this)}></Menu>]});
		}else if('cancelMenu'==type){

			this.setState({addMenu:[]})
		}else if('openInvite'==type){
			this.setState({addInvite:[<Invite callBack={this.menuCallBack.bind(this)}></Invite>],addMenu:[]})
		}else if('cancelInvite'==type){
			this.setState({addInvite:[]})
		}else if('openLoading'==type){
			this.setState({addLoading:[<Loading></Loading>]});
		}else if('cancelLoading'==type){
			this.setState({addLoading:[]});
		}else if('openLogin'==type){
			this.setState({addLogin:[<Login callBack={this.menuCallBack.bind(this)}></Login>]})
		}else if('cancelLogin'==type){
			this.setState({addLogin:[]})
		}
	}
	openMenu(e){
		let ev=e||window.event;
		ev.stopPropagation();
		
		this.menuCallBack('openMenu');
		return false;
	}
	cancelMenu(){
		this.menuCallBack('cancelMenu');
	}
	goRoom(){

		window.open("room.html");
	}
	connect(){
//		let eos=EOS()
	}
	componentWillMount(){
		
		let _this=this;
//		_this.connect();
		 this.loadRank(0);
//		_this.menuCallBack('openLoading');
//		setTimeout(function(){
//			_this.menuCallBack('cancelLoading');
//		},1500);
	}
	
  render() {
  	let lang=localStorage.getItem("lang");//语种
  	lang=lang?lang:"CN";
	let arr=[
		<RoomList theme='1' key='1' callBack={this.goRoom.bind(this)}></RoomList>,
		<RoomList theme='2' key='2' callBack={this.goRoom.bind(this)}></RoomList>,
		<RoomList theme='3' key='3' callBack={this.goRoom.bind(this)}></RoomList>,
		<RoomList theme='4' key='4' callBack={this.goRoom.bind(this)}></RoomList>
	]
 
    return (
    	<div className="wrap" onClick={this.cancelMenu.bind(this)}>
	      <div className="app">
	        <div className="nav-wrap flex">
	        	<div>
	        		<img className="nav-rb" src={superRedbacket2x}/>
							<span className="nav-text">{lang=="CN"?"超級红包":"Super Hongbao"}</span>
	        	</div>
	        		<img className="nav-menu" src={menu2x} onClick={this.openMenu.bind(this)}/>
	        </div>
	        {arr}
	        <div className="flex fun-wrap">
	        	<li>
	        		<a href='' className="flex-start fun-1">
		        		<div className="img-box"><img src={signInIocn}/></div>
		        		<div>
		        			<p className="text-shadow">{lang=="CN"?"签到领红包":"Sign-in"}</p>
		        			<p>09:33:33</p>
		        		</div>
	        		</a>
	        	</li>
	        	<li>
	        		<a href='' className="flex fun-2">
		        		<div className="img-box">
		        			<img src={stockSrc}/>
		        			<span>{lang=="CN"?"成为股东":"Equity Swap"}</span>
		        		</div>
	        		</a>
	        	</li>
	        </div>
	      </div>
	      <Rank rankData={this.state.rankData}></Rank>
	      {this.state.addMenu}
	      {this.state.addEos}
	      {this.state.addInvite}
	      {this.state.addLoading}
		  {this.state.addLogin}
      </div>
    );
  }
}

export default App;
